@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root
{
   --white-25: rgba(255, 255, 255, 0.25);
   --white-40: rgba(255, 255, 255, 0.40);
}
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: 'Poppins', cursive;
}
.App {
  text-align: center;
 
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.component
{
  height: 100vh;
  width:100%;
  background-image: url(/static/media/wallpaper-6.ffe7d07b.jpg);
  background-size: 100% 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0px;
  padding: 0px;
  border: none;
}

.blur-back
{
  height: 100%;
  width:100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container
{
  height: 95%;
  width: 80%;
  position: relative;
  -webkit-backdrop-filter: blur( 50px);
          backdrop-filter: blur( 50px);
  border-radius: 30px;
  display: flex;
  align-items: center;
  z-index: 1;
  margin: 0px;
  padding: 0px;
  outline: none;
  padding: 20px;
}

.rightcontainer
{
  height:100%;
  width: 50%;
  background-image: url(/static/media/logo512.850d3e9e.png);
  background-position: center;
  background-repeat: no-repeat;
  z-index: 2;
  bottom:10px;
}

.box
{
  background-color: rgba(255, 255, 255, 0.26);
  opacity: 1;
  width: 100%;
  height: 100%;
  padding: 10px;
  color: #fff;
  -webkit-backdrop-filter: blur(7px);
          backdrop-filter: blur(7px);
}

.head-name
{
  color: rgb(255, 251, 0);
}
.temp-main
{
  color: rgb(255, 0, 119);
}

.minmax
{
  padding: 20px;
  color: rgb(0, 4, 59);
}

.desc
{
  color: rgb(255, 81, 0);
}

.box-2
{
   color: rgb(255, 255, 255);
   padding: 10px;
}

.leftcontainer
{
 position: absolute;
  height: 80%;
  width: 500px;
  background: rgba(255, 255, 255, 0.185);
  margin-left:700px;
  margin-top: 60px;
  padding: 40px;
  z-index: 4;
}
.namehead
{
  color: #fff;
  font-size: 44px;
}

.input-block
{
  width: 80%;
   margin: 20px;
   transition: 0.5s all linear;
}

.output-block
{
  margin: 20px;
}

.icons-weath
{
  color: rgb(61, 0, 173);
}

form
{
    margin-top: 3rem;
}

form input[name="city"],
form input[name="country"]
{
    background: transparent;
    color: white;
    border: 0;
    border-bottom: 2px solid rgb(255, 255, 255);
    border-radius: 0;
    transition: 0.5s all linear;
}

form input:focus
{
    background-color: transparent;
    box-shadow: none important!;
}


